import Vue from 'vue'
import Component from 'vue-class-component'
import './content.css'
import HeadView from '../components/headView.js'
import OtherInputView from '../components/otherInputView.js'
import DateView  from '../components/dateView.js'

import { addNewRecord } from './methods.js'

@Component
export default class AddRecord extends Vue {
	wrapperHeight = 0
  myAddress = ''
  money = ''
  recever = ''
  time = ''
	created() {
    
    eventBus.$on('addSuccess',this.handleCallback)
      var date = new Date();
        var month = date.getMonth() + 1;
        var strDate = date.getDate();
        if (month >= 1 && month <= 9) {
            month = "0" + month;
        }
        if (strDate >= 0 && strDate <= 9) {
            strDate = "0" + strDate;
        }
        this.time = date.getFullYear() + "-" + month + "-" + strDate;
    this.wrapperHeight = document.documentElement.clientHeight
   }

  handleCallback(address){

        this.$router.push({name:'ManagerRecord',params:{address:address}})

  }

   get caculateHeight() {
    return 'height:' + String(this.wrapperHeight) + 'px'
  }

  handlerSure (){

    this.myAddress =this.myAddress.replace(/\s+/g,"");
    this.recever =this.recever.replace(/\s+/g,"");
    this.money =this.money.replace(/\s+/g,"");
    this.time =this.time.replace(/\s+/g,"");

      if (!this.myAddress || this.myAddress=="") {
        alert("请输入您的钱包地址");
        return;
      }
      if (!this.money || this.money=="") {
        alert("请输入应收账款总额");
        return;
      }
      if (!this.time || this.time=="") {
        alert("请选择到账日期");
        return;
      }
      if (!this.recever || this.recever=="") {
        alert("请输入收款方的钱包地址");
        return;
      }
      if (this.recever === this.myAddress) {
        alert("付款方钱包地址与收款方钱包地址不能相同");
        return;
      }

    var model = {
      payer:this.myAddress,
      recevier:this.recever,
      money:this.money,
      time:this.time
    }
    addNewRecord(model)
  }

  handleCheckRecord(){
    this.myAddress =this.myAddress.replace(/\s+/g,"");
    if (!this.myAddress || this.myAddress=="") {
        alert("请先输入您的钱包地址");
        return;
      }
    eventBus.$emit('addSuccess', this.myAddress);
  }

  handlerGoManager (){
    this.$router.push({name:'ManagerRecord'})
  }

  render () {
    return (
     <div class = "mainContent" style ={this.caculateHeight}>
     	<HeadView onBackHome={()=>{this.$router.go(-1)}} onManagerRecord = {this.handlerGoManager}></HeadView>
     	<p style='margin-top:150px;font-size:20px;text-align:center;color:#fff'>新增记录</p>
     	<OtherInputView onValue = {value=>{this.myAddress = value}} title='您的钱包地址' placeholder='请输入您的钱包地址'/>

     	<OtherInputView onValue = {value=>{this.money = value}}  title='应收账款' placeholder='请输入应收账款总额'/>
     	<DateView onTimeValue = {value=>{this.time = value }} title='到账日期'></DateView>
     	<OtherInputView onValue = {value=>{this.recever = value}}  title='收款方钱包地址' placeholder='请输入收款方的钱包地址	'/>

     	<div onClick={this.handlerSure} style='border-radius:5px;height:40px;background:#00998b;color:#fff;width:24%;margin-left:38%;text-align:center;line-height:40px;margin-top:20px'>确定</div>

     	<span onClick={this.handleCheckRecord} style='position:absolute;padding-top:20px;width:100%;text-align:center;font-size:12px;color:#fff'>若确认钱包已交易成功，请点击此处<font style='color:#ff4545;font-size:12px'>查看记录</font></span>
      </div>
    )
  }
}


