import Vue from 'vue'
import Component from 'vue-class-component'
import './components.css'

@Component({
    props:{
        title:String
    }
})
export default class DateView extends Vue {
    currentDate = ''
    chooseDate = ''

    created (){
        this.getNowFormatDate()
    }

    handleDateChoose (e){
        this.chooseDate = e.target.value
        this.$emit('timeValue',this.chooseDate)
    }

    getNowFormatDate() {
        var date = new Date();
        var month = date.getMonth() + 1;
        var strDate = date.getDate();
        if (month >= 1 && month <= 9) {
            month = "0" + month;
        }
        if (strDate >= 0 && strDate <= 9) {
            strDate = "0" + strDate;
        }
        this.currentDate = date.getFullYear() + "-" + month + "-" + strDate;
    }

    render (){
        return (
            <div>
                <dl class='otherInputClass'>
            <dd>
            {this.title}
            </dd>
            <dt>
                <input type='date' style='color:#000' class='otherInputCSS' value={this.currentDate} onChange={value => { this.handleDateChoose(value) }} />
            </dt>
        </dl>
            </div>
        )
    }
}


