import Vue from 'vue'
import Component from 'vue-class-component'
import './components.css'

@Component({
	props:{
		title:String,
		placeholder:String
	}
})
export default class OtherInputView extends Vue {
    handleChange (val){
        this.$emit('value',val.target.value)
    }

  render () {
    return (
     	<div>
     		<dl class='otherInputClass'>
     		<dd>
     		{this.title}
     		</dd>
     		<dt>
     			<input class='otherInputCSS' onChange={value => { this.handleChange(value) }} placeholder={this.placeholder} />
     		</dt>
     	</dl>
     	</div>
    )
  }
}





